@import '@material/slider/dist/mdc.slider.css';

:root {
  --mdc-theme-secondary: rgb(233 108 76);
}

.mdc-form-field{
      color: white;
      display: inline-block;
      font: 1rem 'Fira Sans', sans-serif;
}

.fixlevel{
      display: inline-block;
      margin-right:9px;
      margin-top: 8px;
      margin-left: 8px;
}

.fixlevel a{
      text-decoration: none;
      color: rgb(71 127 127);
      transition: all .2s ease-in-out;
}

.fixlevel a:hover{
      color: rgb(233 108 76);
      transform: scale(1.7);
      text-decoration: underline;
}

.mdc-slider__track-container{
    background-color: rgb(71 127 127) !important;
}

.mdc-slider-label{
    top: 60%;
    position: absolute;
}

.checked {
    color:rgb(233 108 76) !important;
}

#disable_control span, #gofundme span {
    font-size: 17px;
    position: fixed;
    top: 0px;
    right: 0px;
    color: red;
    transition: all .2s ease-in-out;
}

#gofundme span {
    font-size: 30px;
    color: green;
    top: 18px;
    right:10px;
}

#disable_control span:hover {
      color: rgb(233 108 76);
}

#gofundme  span:hover {
      color: rgb(233 108 76);
}

#slider-max {
    margin-left: -50%;
    margin-top: 5px;
}



html, body {
  height: 77px;
  width: 332px;
}

body {
  margin: 0;
  display: flex;
  align-items: center;
  background: #000;
}

main {
  width: 100%;
  margin: 0 20px;
}

.mdc-slider {
  transition: opacity 500ms;
  color: rgb(61 183 183);
  height: 30px;
  display: inline-block;
}

.mdc-slider div {
    display: inline-block;
    margin-right:auto;
    margin-left:auto;
}

.mdc-slider-container{
    width:95%;
}

.mdc-slider__pin-value-marker {
  font-size: 0.575rem;
}

.mdc-slider__thumb-container {
        top: 6px;
}
